import React, { useEffect, useState, useCallback } from 'react'
import { BiArrowBack, BiX } from 'react-icons/bi';
import CustomInput from '../../components/ui/Input/CustomInput';
import { getAllCompanies, getAllIndustry, getAllProfileRole, getAllWorkSkillList, updateCompanyData } from '../../redux/work/workSlice';
import { useDispatch, useSelector } from 'react-redux';
import { arrayTransform, arrayTransform2, convertToTimestamp } from '../../components/utils/globalFunction';
import { cities, countries, jobsCreate, jobsSingleDocument, jobsUpdate, state, updateMasterIndustryData, updateMasterSkillData, updateProfileRoleData } from '../../redux/Global Slice/cscSlice';
import { toast } from 'sonner';
import Button from '../../components/ui/Button/Button';
import { useNavigate, useParams } from 'react-router-dom';
import { getCookie } from '../../components/utils/cookieHandler';
import Modal from '../../components/ui/Modal/Modal';
import { getAllDegree, getAllFieldsOfStudy, getAllSkillList, updateDegreeData, updateFieldsOfStudyData, } from '../../redux/education/educationSlice';
import { addOneData } from '../../redux/Users/userSlice';
import { useLocationFormHandlers } from '../../components/hooks/useLocationFormHandlers';
import StepFirst from './Components/StepFirst';
import StepSecond from './Components/StepSecond';
// import moment from 'moment-timezone';

const PostJob = () => {
    const { id } = useParams()
    const dispatch = useDispatch()
    const navigate = useNavigate()

    const workSelector = useSelector(state => state.work);
    const industrySelector = useSelector(state => state.global);
    const countriesSelector = useSelector(state => state.global);
    const allCompanies = arrayTransform2(workSelector?.getAllCompaniesData?.data?.data || [])
    const allIndustry = arrayTransform(workSelector?.getAllIndustryData?.data?.data || [])
    const allSkills = arrayTransform(industrySelector?.masterSkillsData?.data?.data?.list || [])
    const allProfileRoles = arrayTransform(workSelector?.getAllProfileRoleData?.data?.data || [])
    const countryList = arrayTransform(countriesSelector?.countriesData?.data?.data || [])
    const stateList = arrayTransform(countriesSelector?.stateData?.data?.data || [])
    const citiesList = arrayTransform(countriesSelector?.citiesData?.data?.data || [])


    const [currentStep, setCurrentStep] = useState(1);
    const [submitting, setIsSubmitting] = useState(false)
    const [accessMode, setAccessMode] = useState(getCookie("ACCESS_MODE"));
    const [selectedSkills, setSelectedSkills] = useState([]);
    const [screeningQuestions, setScreeningQuestions] = useState([
        {
            question: "",
            question_type: "single_choice",
            options: [],
            option_format: "alphabetically",
            correct_options: [],
            verification_type: "auto",
            time_limit: 2
        }
    ]);
    const [errors, setErrors] = useState({});
    const [inputFields, setInputFields] = useState({
        name: "", logo_url: ""
    })
    const [addModalState, setAddModalState] = useState({
        isOpen: false,
        type: '',
        field: ''
    });

    const [formData, setFormData] = useState({
        company_id: "",
        industry_id: "",
        job_type: "",
        job_location: "",
        pay_type: "",
        salary_range: "",
        job_title: "",
        job_description: "",
        start_date: "",
        end_date: "",
        current_openings:"",
        required_skills: [],
        address: {
            country: {
                name: "",
                dial_code: "",
                short_name: "",
                emoji: "ðŸ‡®"
            },
            state: {
                name: "",
                code: ""
            },
            city: {
                name: ""
            },
            pin_code: ""
        },
        screening_questions: [],
        isDisable: false,
        isShareAsPost: false
    });

    const [isCreatableIndustry, setIsCreatbleIndustry] = useState(false)

    const { handleLocationSelectChange, fetchDependentData } = useLocationFormHandlers(setFormData, formData, setErrors,);

    // Handle access mode changes
    useEffect(() => {
        const handleStorageChange = () => {
            const newAccessMode = getCookie("ACCESS_MODE");
            setAccessMode(newAccessMode);
            if (newAccessMode == 5) {
                navigate(`/user/opportunitiess`);
            }
        };

        handleStorageChange();
        window.addEventListener('storage', handleStorageChange);
        return () => window.removeEventListener('storage', handleStorageChange);
    }, [navigate]);

    // Fetch initial data
    useEffect(() => {
        dispatch(getAllCompanies())
        dispatch(countries())
    }, [dispatch])

    // Populate selected skills from skill IDs
    const populateSelectedSkills = useCallback((skillIds) => {
        if (allSkills.length > 0 && skillIds.length > 0) {
            const matchedSkills = allSkills.filter(skill =>
                skillIds.includes(skill.value)
            );
            setSelectedSkills(matchedSkills);
        }
    }, [allSkills]);

    const country_id = formData?.address?.country?.short_name
    const city_code = formData?.address?.state?.name

    const fetchJobData = useCallback(async () => {
        if (!id) return;

        try {
            const res = await dispatch(jobsSingleDocument({ _id: id, populate: 'company_id:created_by_users|industry_id:created_by_users' })).unwrap()
            const jobData = res?.data;

            const addressData = jobData.work_location || jobData.address || {
                country: { name: "", dial_code: "", short_name: "", emoji: "ðŸ‡®" },
                state: { name: "", code: "" },
                city: { name: "" },
                pin_code: ""
            };

            setFormData({
                ...jobData,
                address: addressData,
                company_id: res?.data?.company_id?._id,
                industry_id: res?.data?.industry_id?._id,
                start_date: res?.data?.start_date ? new Date(res.data.start_date).toISOString().split("T")[0] : "",
                end_date: res?.data?.end_date ? new Date(res.data.end_date).toISOString().split("T")[0] : "",
            });

            if (res?.data?.company_id) {
                dispatch(getAllIndustry({ company_id: res?.data?.company_id?._id, created_by_users: res?.data?.company_id?.created_by_users }));
                setIsCreatbleIndustry(res?.data?.company_id?.created_by_users)

            }

            if (res?.data?.industry_id) {
                dispatch(getAllProfileRole({ industry_id: res?.data?.industry_id?._id, created_by_users: res?.data?.industry_id?.created_by_users }));

            }

            if (jobData.screening_questions && jobData.screening_questions.length > 0) {
                setScreeningQuestions(jobData.screening_questions);
            }

            if (jobData.required_skills && jobData.required_skills.length > 0) {
                populateSelectedSkills(jobData.required_skills);
            }
        } catch (error) {
            console.error("Error fetching job data:", error);
            toast.error(error?.message || "Failed to fetch job data");
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [id, dispatch]);

    useEffect(() => {
        if (country_id) {
            dispatch(state({ country_code: country_id }))
        }
        if (city_code) {
            dispatch(cities({ state_name: city_code }))
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [dispatch, country_id, city_code])



    // Fetch job data when component mounts with an ID
    useEffect(() => {
        if (id) {
            fetchJobData();
        }
    }, [id, fetchJobData]);
    useEffect(() => {
        if (id && allSkills.length > 0 && formData.required_skills.length > 0 && selectedSkills.length === 0) {
            populateSelectedSkills(formData.required_skills);
        }
    }, [allSkills, formData.required_skills, id, populateSelectedSkills, selectedSkills.length]);


    const validateStep = (step) => {
        const newErrors = {};

        if (step === 1) {
            if (!formData.company_id) newErrors.company_id = "Company is required";
            if (!formData.industry_id) newErrors.industry_id = "Industry is required";
            if (!formData.job_type) newErrors.job_type = "Job type is required";
            if (!formData.job_location) newErrors.job_location = "Job location is required";
            if (!formData.pay_type) newErrors.pay_type = "Pay type is required";
            if (
                !formData.salary_range &&
                !(formData.job_type === 'internship' || formData.pay_type === 'unpaid')
            ) {
                newErrors.salary_range = "Salary range is required";
            }

            if (!formData.start_date) newErrors.start_date = "Start date is required";
            if (!formData.end_date) newErrors.end_date = "End date is required";
            if (!formData.current_openings) newErrors.current_openings = "current opening is required";



            if (!formData.address?.country?.name) newErrors.country = "Country is required";
            if (!formData.address?.state?.name) newErrors.state = "State is required";
            if (!formData.address?.city?.name) newErrors.city = "City is required";
            if (!formData.address?.pin_code) newErrors.pin_code = "Pin No is required";

        }

        if (step === 2) {
            if (!formData.job_title) newErrors.job_title = "Job title is required";
            if (!formData.job_description) newErrors.job_description = "Job description is required";
            if (formData.job_description && formData.job_description.length < 50) {
                newErrors.job_description = "Description should be at least 50 characters";
            }
            if (formData.required_skills.length === 0) {
                newErrors.required_skills = "At least one skill is required";
            }
        }

        if (step === 3) {
            if (screeningQuestions.length === 0) {
                newErrors.screening_questions = "At least one screening question is required";
            } else {
                screeningQuestions.forEach((q, qIndex) => {
                    if (!q.question || q.question.trim() === "") {
                        newErrors[`screening_question_${qIndex}`] = "Question is required";
                    }

                    // For theoretical questions, no additional validation needed beyond the question text
                    if (q.question_type === 'theoretical') {
                        return;
                    }

                    if (q.options.length < 2) {
                        newErrors[`screening_options_${qIndex}`] = "At least two options are required";
                    }

                    q.options.forEach((option, oIndex) => {
                        if (!option || option.trim() === "") {
                            newErrors[`screening_option_${qIndex}_${oIndex}`] = "Option cannot be empty";
                        }
                    });

                    if (q.correct_options.length === 0) {
                        newErrors[`screening_correct_${qIndex}`] = "At least one correct option is required";
                    }
                });
            }
        }

        setErrors(newErrors);
        return Object.keys(newErrors).length === 0;
    };



    const handleInputChange = (field, value) => {
        setFormData(prev => {
            const updatedFormData = {
                ...prev,
                [field]: value,
            };
            let newErrors = { ...errors };
            if (updatedFormData.start_date && updatedFormData.end_date) {
                const start = new Date(updatedFormData.start_date);
                const end = new Date(updatedFormData.end_date);
                if (end < start) {
                    newErrors.end_date = "End date cannot be before Start date";
                } else {
                    delete newErrors.start_date;
                    delete newErrors.end_date;
                }
            }
            setErrors(newErrors);
            return updatedFormData;
        });
        if (errors[field]) {
            setErrors(prev => {
                const newErrors = { ...prev };
                delete newErrors[field];
                return newErrors;
            });
        }
    };



    const handleSelectChange = async (field, selectedOption) => {
        setFormData(prev => ({
            ...prev,
            [field]: selectedOption.value
        }));

        console.log("field, selectedOption", field, selectedOption)
        if (field === 'company_id') {
            setIsCreatbleIndustry(selectedOption?.created_by_users)
            
        }

        const value = selectedOption?.value;

        switch (field) {
            case 'institution_id':
                await dispatch(getAllDegree({ institution_id: value, created_by_users: selectedOption?.created_by_users }));
                break;
            case 'degree_id':
                await dispatch(getAllFieldsOfStudy({ degree_id: value, created_by_users: selectedOption?.created_by_users }));
                break;
            case 'field_of_studies':
                await dispatch(getAllSkillList({ study_id: value, created_by_users: selectedOption?.created_by_users }));
                break;
            case 'company_id':
                await dispatch(getAllIndustry({ company_id: value, created_by_users: selectedOption?.created_by_users }));
                break;
            case 'industry_id':
                await dispatch(getAllProfileRole({ industry_id: value, created_by_users: selectedOption?.created_by_users }));
                break;
            case 'profile_role_id':
                await dispatch(getAllWorkSkillList({ profile_role_id: value, created_by_users: selectedOption?.created_by_users }));
                break;
            default:
                break;
        }

        // Clear error for this field
        if (errors[field]) {
            setErrors(prev => {
                const newErrors = { ...prev };
                delete newErrors[field];
                return newErrors;
            });
        }
    };

    const handleSkillSelect = (selectedOption) => {
        if (selectedOption && !formData.required_skills.includes(selectedOption.value)) {
            setFormData(prev => ({
                ...prev,
                required_skills: [...prev.required_skills, selectedOption.value]
            }));
            setSelectedSkills(prev => [...prev, selectedOption]);

            if (errors.required_skills) {
                setErrors(prev => {
                    const newErrors = { ...prev };
                    delete newErrors.required_skills;
                    return newErrors;
                });
            }
        }
    };

    const removeSkill = (skillId) => {
        setFormData(prev => ({
            ...prev,
            required_skills: prev.required_skills.filter(id => id !== skillId)
        }));
        setSelectedSkills(prev => prev.filter(skill => skill.value !== skillId));
    };

    const handleScreeningQuestionChange = (index, field, value) => {
        const updatedQuestions = [...screeningQuestions];
        updatedQuestions[index][field] = value;
        setScreeningQuestions(updatedQuestions);

        // Clear specific errors when field is updated
        if (field === 'question' && errors[`screening_question_${index}`]) {
            setErrors(prev => {
                const newErrors = { ...prev };
                delete newErrors[`screening_question_${index}`];
                return newErrors;
            });
        }
    };

    const addScreeningQuestion = () => {
        setScreeningQuestions([...screeningQuestions, {
            question: "",
            question_type: "single_choice",
            options: [],
            option_format: "alphabetically",
            correct_options: [],
            verification_type: "auto",
            time_limit: 2
        }]);
    };

    const removeScreeningQuestion = (index) => {
        const updatedQuestions = [...screeningQuestions];
        updatedQuestions.splice(index, 1);
        setScreeningQuestions(updatedQuestions);

        // Clear errors for the removed question
        const newErrors = { ...errors };
        Object.keys(newErrors).forEach(key => {
            if (key.startsWith(`screening_question_${index}`) ||
                key.startsWith(`screening_options_${index}`) ||
                key.startsWith(`screening_correct_${index}`) ||
                key.startsWith(`screening_option_${index}_`)) {
                delete newErrors[key];
            }
        });
        setErrors(newErrors);
    };

    const addOption = (questionIndex) => {
        const updatedQuestions = [...screeningQuestions];
        updatedQuestions[questionIndex].options.push("");
        setScreeningQuestions(updatedQuestions);
    };

    const removeOption = (questionIndex, optionIndex) => {
        const updatedQuestions = [...screeningQuestions];
        updatedQuestions[questionIndex].options.splice(optionIndex, 1);

        // Also remove from correct_options if it was selected
        const optionValue = updatedQuestions[questionIndex].options[optionIndex];
        const correctIndex = updatedQuestions[questionIndex].correct_options.indexOf(optionValue);
        if (correctIndex !== -1) {
            updatedQuestions[questionIndex].correct_options.splice(correctIndex, 1);
        }

        setScreeningQuestions(updatedQuestions);

        // Clear error for this specific option
        if (errors[`screening_option_${questionIndex}_${optionIndex}`]) {
            setErrors(prev => {
                const newErrors = { ...prev };
                delete newErrors[`screening_option_${questionIndex}_${optionIndex}`];
                return newErrors;
            });
        }
    };

    const toggleCorrectOption = (questionIndex, optionValue) => {
        const updatedQuestions = [...screeningQuestions];
        const question = updatedQuestions[questionIndex];

        if (question.question_type === 'single_choice') {
            question.correct_options = [optionValue];
        } else {
            const index = question.correct_options.indexOf(optionValue);
            if (index === -1) {
                question.correct_options.push(optionValue);
            } else {
                question.correct_options.splice(index, 1);
            }
        }

        setScreeningQuestions(updatedQuestions);

        // Clear correct option error if it exists
        if (errors[`screening_correct_${questionIndex}`]) {
            setErrors(prev => {
                const newErrors = { ...prev };
                delete newErrors[`screening_correct_${questionIndex}`];
                return newErrors;
            });
        }
    };

    const nextStep = () => {
        if (validateStep(currentStep)) {
            if (currentStep < 3) setCurrentStep(currentStep + 1);
        } else {
            const firstErrorKey = Object.keys(errors)[0];
            if (firstErrorKey) {
                const element = document.querySelector(`[data-error="${firstErrorKey}"]`);
                if (element) {
                    element.scrollIntoView({ behavior: 'smooth', block: 'center' });
                }
            }
        }
    };

    const prevStep = () => {
        if (currentStep > 1) setCurrentStep(currentStep - 1);
    };

    const getNestedValue = (obj, path, fallback = '') => {
        const keys = path.split('.');
        let value = obj;
        for (const key of keys) {
            value = value?.[key];
            if (value === undefined) return fallback;
        }
        return value || fallback;
    };

    const handleSubmit = async () => {
        if (!validateStep(3)) {
            const firstErrorKey = Object.keys(errors)[0];
            if (firstErrorKey) {
                const element = document.querySelector(`[data-error="${firstErrorKey}"]`);
                if (element) {
                    element.scrollIntoView({ behavior: 'smooth', block: 'center' });
                }
            }
            return;
        }

        setIsSubmitting(true);

        const finalData = {
            company_id: formData?.company_id || "",
            industry_id: formData?.industry_id || "",
            job_type: formData?.job_type || "",
            job_location: formData?.job_location || "",
            pay_type: formData?.pay_type || "",
            salary_range: formData?.salary_range || "",
            job_title: formData?.job_title || "",
            job_description: formData?.job_description || "",
            required_skills: formData?.required_skills || [],
            work_location: formData?.address || "",
            isDisable: false,
            screening_questions: screeningQuestions,
            start_date: convertToTimestamp(formData?.start_date) || "",
            end_date: convertToTimestamp(formData?.end_date) || "",
            isShareAsPost: formData?.isShareAsPost,
            current_openings:formData?.current_openings
        };

        if (id) {
            finalData._id = id;
        }

        const action = id ? jobsUpdate : jobsCreate;

        try {
            const res = await dispatch(action(finalData)).unwrap();
            toast.success(res?.message || res?.success || (id ? "Job updated successfully!" : "Job posted successfully!"));

            // Reset form and navigate
            setCurrentStep(1);
            navigate(`/user/opportunitiess`);

            setFormData({
                company_id: "",
                industry_id: "",
                job_type: "",
                job_location: "",
                pay_type: "",
                salary_range: "",
                job_title: "",
                job_description: "",
                required_skills: [],
                address: {
                    country: { name: "", dial_code: "", short_name: "", emoji: "ðŸ‡®" },
                    state: { name: "", code: "" },
                    city: { name: "" },
                    pin_code: ""
                },
                screening_questions: [],
                isDisable: false,
                start_date: "",
                end_date: ""
            });

            setSelectedSkills([]);
            setScreeningQuestions([{
                question: "",
                question_type: "single_choice",
                options: [],
                option_format: "alphabetically",
                correct_options: [],
                verification_type: "auto",
                time_limit: 2
            }]);
            setErrors({});
        } catch (error) {
            console.error("Error submitting job:", error);
            toast.error(error?.message || (id ? "Failed to update job. Please try again." : "Failed to post job. Please try again."));
        } finally {
            setIsSubmitting(false);
        }
    };


    const handleAddItem = async () => {
        try {
            let type = '';
            let updateAction = null;
            let selectField = addModalState.field;

            switch (addModalState.type) {

                case 'degree':
                    type = 'degrees';
                    updateAction = updateDegreeData;
                    break;
                case 'field':
                    type = 'fields-of-studies';
                    updateAction = updateFieldsOfStudyData;
                    break;
                case 'skill':
                    type = 'skills';
                    updateAction = updateMasterSkillData;
                    break;
                case 'companies':
                    type = "companies"
                    updateAction = updateCompanyData;
                    break;
                case 'industries':
                    type = 'industries'
                    updateAction = updateMasterIndustryData;
                    break;
                case 'profile-roles':
                    type = "profile-roles"
                    updateAction = updateProfileRoleData;
                    break;
                default:
                    return;
            }

            const res = await dispatch(addOneData({ type, ...inputFields })).unwrap();
            //  console.log("res?.data:----", res?.data)

            // Update the respective data list
            dispatch(updateAction({
                _id: res.data._id,
                name: res.data.name,
                created_by_users: res?.data?.created_by_users
            }));

            if (selectField) {
                if (selectField !== 'skills_acquired') {
                    setFormData(prev => ({
                        ...prev,
                        [selectField]: res.data._id
                    }));

                    const iscreated_by_users = res.data.created_by_users;
                    fetchDependentData(selectField, res.data._id, iscreated_by_users);
                }
                else {
                    setFormData(prev => ({
                        ...prev,
                        skills_acquired: [...(prev.skills_acquired || []), res.data._id]
                    }));
                }
            }

            setAddModalState({ isOpen: false, type: '', field: '' });
            setInputFields({ name: "", logo_url: "" });

        } catch (error) {
            toast.error(error);
        }
    };

    const renderStep1 = () => (
        <div className="space-y-3">
            <StepFirst
                allCompanies={allCompanies}
                allIndustry={allIndustry}
                formData={formData}
                setFormData={setFormData}
                handleInputChange={handleInputChange}
                handleSelectChange={handleSelectChange}
                errors={errors}
                setAddModalState={setAddModalState}
                countryList={countryList}
                handleLocationSelectChange={handleLocationSelectChange}
                getNestedValue={getNestedValue}
                stateList={stateList}
                citiesList={citiesList}
                setInputField={setInputFields}
                isCreatableIndustry={isCreatableIndustry}
            />
        </div>
    );

    const renderStep2 = () => (
        <StepSecond
            handleSelectChange={handleSelectChange}
            allProfileRoles={allProfileRoles}
            allSkills={allSkills}
            formData={formData}
            errors={errors}
            setAddModalState={setAddModalState}
            handleInputChange={handleInputChange}
            selectedSkills={selectedSkills}
            removeSkill={removeSkill}
            handleSkillSelect={handleSkillSelect}
            isCreatableIndustry={isCreatableIndustry}
        />
    );

    const renderStep3 = () => (
        <div className="space-y-6">
            <div>
                <label className="block text-sm font-medium text-gray-700 mb-4">
                    Screening Questions <span className="text-red-500">*</span>
                </label>
                <p className="text-sm text-gray-500 mb-4">
                    Add screening questions to filter candidates. All questions are required.
                </p>

                {errors.screening_questions && (
                    <p className="text-sm text-red-500 mb-4 bg-red-50 p-2 rounded-md">
                        {errors.screening_questions}
                    </p>
                )}

                {screeningQuestions.map((question, qIndex) => {
                    const questionError = errors[`screening_question_${qIndex}`];
                    const optionsError = errors[`screening_options_${qIndex}`];
                    const correctError = errors[`screening_correct_${qIndex}`];

                    return (
                        <div
                            key={qIndex}
                            className={`mb-6 p-4 border rounded-lg ${questionError || optionsError || correctError ? 'border-red-300 bg-red-50' : 'border-gray-200'}`}
                        >
                            <div className="flex justify-between items-center mb-3">
                                <h3 className="font-medium">Question {qIndex + 1} <span className="text-red-500">*</span></h3>
                                <button
                                    onClick={() => removeScreeningQuestion(qIndex)}
                                    className="text-red-500 hover:text-red-700"
                                >
                                    <BiX className="h-5 w-5" />
                                </button>
                            </div>

                            <div className="mb-3">
                                <CustomInput
                                    value={question.question}
                                    onChange={(e) => handleScreeningQuestionChange(qIndex, 'question', e.target.value)}
                                    placeholder="Enter your question"
                                    className="w-full h-10"
                                    error={questionError}
                                    required
                                />
                            </div>

                            <div className="mb-3">
                                <label className="block text-sm font-medium text-gray-700 mb-1">Question Type</label>
                                <select
                                    value={question.question_type}
                                    onChange={(e) => handleScreeningQuestionChange(qIndex, 'question_type', e.target.value)}
                                    className="w-full p-2 border border-gray-300 rounded-md"
                                >
                                    <option value="single_choice">Single Choice</option>
                                    <option value="multi_choice">Multiple Choice</option>
                                    <option value="theoretical">Theoretical</option>
                                </select>
                            </div>

                            {question.question_type === 'theoretical' ? (
                                <div className="mb-3">
                                    <label className="block text-sm font-medium text-gray-700 mb-1">Expected Answer (Optional)</label>
                                    <textarea
                                        value={question.expected_answer || ""}
                                        onChange={(e) => handleScreeningQuestionChange(qIndex, 'expected_answer', e.target.value)}
                                        rows={3}
                                        className="w-full p-2 border border-gray-300 rounded-md"
                                        placeholder="Describe what you're looking for in an answer (optional)"
                                    />
                                </div>
                            ) : (
                                <div className="space-y-2">
                                    <div className="flex justify-between items-center">
                                        <label className="block text-sm font-medium text-gray-700">Options <span className="text-red-500">*</span></label>
                                        {optionsError && (
                                            <span className="text-sm text-red-500">{optionsError}</span>
                                        )}
                                    </div>

                                    {question.options.map((option, oIndex) => {
                                        const optionError = errors[`screening_option_${qIndex}_${oIndex}`];

                                        return (
                                            <div key={oIndex} className="flex items-center">
                                                <input
                                                    type={question.question_type === 'single_choice' ? 'radio' : 'checkbox'}
                                                    checked={question.correct_options.includes(option)}
                                                    onChange={() => toggleCorrectOption(qIndex, option)}
                                                    className="mr-2 "
                                                    name={`question-${qIndex}`}
                                                />
                                                <div className="flex-1">
                                                    <CustomInput
                                                        value={option}
                                                        onChange={(e) => {
                                                            const newValue = e.target.value;
                                                            const updatedQuestions = [...screeningQuestions];
                                                            if (updatedQuestions[qIndex].correct_options.includes(option)) {
                                                                const correctIndex = updatedQuestions[qIndex].correct_options.indexOf(option);
                                                                updatedQuestions[qIndex].correct_options[correctIndex] = newValue;
                                                            }

                                                            updatedQuestions[qIndex].options[oIndex] = newValue;
                                                            setScreeningQuestions(updatedQuestions);
                                                        }}
                                                        placeholder={`Option ${oIndex + 1}`}
                                                        error={optionError}
                                                        required
                                                    />
                                                </div>
                                                <button
                                                    onClick={() => removeOption(qIndex, oIndex)}
                                                    className="ml-2 text-red-500 hover:text-red-700"
                                                >
                                                    <BiX className="h-5 w-5" />
                                                </button>
                                            </div>
                                        );
                                    })}
                                    <button
                                        onClick={() => addOption(qIndex)}
                                        className="mt-2 text-blue-500 hover:text-blue-700 text-sm"
                                    >
                                        + Add Option
                                    </button>

                                    {correctError && (
                                        <p className="text-sm text-red-500 mt-2">{correctError}</p>
                                    )}
                                </div>
                            )}

                            <div className="mt-3">
                                <label className="block text-sm font-medium text-gray-700 mb-1">Time Limit (minutes) <span className="text-red-500">*</span></label>
                                <input
                                    type="number"
                                    value={question.time_limit}
                                    onChange={(e) => handleScreeningQuestionChange(qIndex, 'time_limit', parseInt(e.target.value) || 0)}
                                    min="1"
                                    className="w-full p-2 border border-gray-300 rounded-md"
                                    required
                                />
                            </div>
                        </div>
                    );
                })}

                <button
                    onClick={addScreeningQuestion}
                    className="mt-2 text-blue-500 hover:text-blue-700 flex items-center"
                >
                    <span className="text-lg mr-1">+</span> Add Another Question
                </button>
            </div>
            {!id && (
                <CustomInput type={`checkbox`} label={`Share As a post`} checked={formData?.isShareAsPost} onChange={(e) => setFormData((prev) => ({ ...prev, isShareAsPost: e.target.checked }))} />
            )}
        </div>
    );

    return (
        <div className='p-4'>
            <div className="min-h-screen">
                <div className="max-w-4xl mx-auto">
                    <div className="">
                        <div className="flex justify-between items-center mb-8">
                            <button onClick={() => navigate(-1)} className="p-1 rounded hover:bg-gray-100">
                                <BiArrowBack size={32} />
                            </button>
                            <h1 className="text-2xl font-semibold text-[#000000E6]">
                                {id ? 'Edit Job' : 'Post a Job'}
                            </h1>
                            <span className="text-sm text-gray-500">{currentStep}/3</span>
                        </div>

                        <div className="mb-8">
                            {currentStep === 1 && renderStep1()}
                            {currentStep === 2 && renderStep2()}
                            {currentStep === 3 && renderStep3()}
                        </div>

                        <div className="flex justify-between mt-8 gap-3">
                            {currentStep > 1 && (
                                <button
                                    onClick={prevStep}
                                    className="px-4 py-2 bg-gray-200 text-gray-700 rounded-md hover:bg-gray-300"
                                >
                                    Back
                                </button>
                            )}

                            {currentStep < 3 ? (
                                <button
                                    onClick={nextStep}
                                    className="px-4 py-2 bg-blue-500 text-white rounded-md hover:bg-blue-600 ml-auto"
                                >
                                    Next
                                </button>
                            ) : (
                                <>

                                    <Button
                                        onClick={handleSubmit}
                                        loading={submitting}
                                        className="px-4 py-2 bg-green-500 text-white rounded-md hover:bg-green-600 ml-auto"
                                    >
                                        {id ? 'Update Job' : 'Post Job'}
                                    </Button>
                                </>
                            )}
                        </div>
                    </div>
                </div>
            </div>

            <Modal
                isOpen={addModalState.isOpen}
                title={`Add ${addModalState.type}`}
                onClose={() => {
                    setAddModalState({ isOpen: false, type: '', field: '' });
                    setInputFields({ name: "", logo_url: "" });
                }}
                handleSubmit={handleAddItem}
            >
                <div className='space-y-3'>
                    <CustomInput
                        className="w-full h-10"
                        label="Enter Name"
                        required
                        placeholder="Enter name"
                        value={inputFields?.name}
                        onChange={(e) => setInputFields(prev => ({ ...prev, name: e.target.value }))}
                    />
                </div>
            </Modal>
        </div>
    )
}

export default PostJob